@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Open+Sans&display=swap");

html {
  overflow-y: scroll;
}

* {
  margin: 0;
  font-family: "Mulish", sans-serif;
}
/* NAVBAR */
.navbar {
  padding: 20px;
  margin: 0px auto;
  align-items: center;
  display: flex;
  border-bottom: 5px transparent #1f1f1f;
  background-color: #121212;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.navbar h3 {
  color: #fafafa;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  padding: 6px;
  text-decoration: none;
  color: #fafafa;
}

.navbar a:hover {
  color: #a1b3c2;
}

/* FOOTER */

.footer {
  display: flex;
  padding: 1rem;
  background-color: #121212;
}

.footer .right-section {
  margin-left: auto;
}

.footer p {
  color: #fafafa;
}

/* HOME */

.home-header {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10px;
  background: #0f4e61;
}

.home-header h1 {
  color: #e9f1f7;
  font-size: xx-large;
}

.home-header mark {
  color: rgb(21, 197, 115);
  background-color: transparent;
}

.home-main {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  background: #040f16;
}

.home-main h2 {
  padding: 4rem;
  color: #e9f1f7;
  font-size: xx-large;
}

.home-main mark {
  background-color: transparent;
  color: hotpink;
}
